/**
 * @fileoverview Main 3D Stage declaration
 */

/* eslint import/no-webpack-loader-syntax: off */

import * as THREE from 'three'
import glslify from 'glslify'
import merge from 'mout/object/merge'

import browser from './helpers/browser'
import fboHelper from './3d/fboHelper'
import state from './State'
import core from './core/settings'
import postprocessing from './3d/postprocessing/postprocessing'
import controls from './controls'
import pointCloudConfiguration from './pointCloudConfiguration'
import lights from './3d/lights'
import particles from './particles'
import v from './transitionParticle/transitionParticle'
import ambientParticle from './ambientParticle/ambientParticle'
import transitionParticle from './transitionParticle/transitionParticleModel'
import p from './55'

let y = 0
let w = 0
let _renderer
let __camera
let _camera
let _scene

let module = Object.create(core)

function checkIsSupported(e) {
  if (window.WebGLRenderingContext) {
    try {
      return getGlContext(e)
    } catch (e) {
      return false
    }
  }
  return false
}

function getGlContext(canvas) {
  if (!module.gl) {
    const gl = state.gl = canvas.getContext('webgl') || canvas.getContext('experimental-webgl')
    if ((gl.getExtension('OES_texture_float') || gl.getExtension('OES_texture_half_float')) && gl.getParameter(gl.MAX_VERTEX_TEXTURE_IMAGE_UNITS)) {
      module.renderTargetFloatType = browser.isIOS || !gl.getExtension('OES_texture_float') ? THREE.HalfFloatType : THREE.FloatType
    } else {
      module.useFloatPacking = true
    }
    return true
  }
  return false
}

function init(options) {
  module = merge(core, options)

  if (module.crossOrigins) {
    for (let t = 0, n = module.crossOrigins.length; t < n; t++) {
      let i = module.crossOrigins[t]
      state.loader.setCrossOrigin(i.url, i.value)
    }
  }
  
  state.mouse2d = new THREE.Vector3
  state.mouse3d = new THREE.Vector4
  state.prevMouse3d = new THREE.Vector4

  if (navigator.userAgent.match(/Node\.js/i) && window && window.reactSnapshotRender) return;

  if (browser.isAndroid) return;

  if (checkIsSupported(options.canvas) == false) return;
  
  main()

  transitionParticle.init()

  lights.init()
  _scene.add(lights.container)

  ambientParticle.init()
  _scene.add(ambientParticle.mesh)

  v.init()
  _scene.add(v.mesh)

  module.hasInitialized = true
}

function loadBasicAssets(callback) {
  state.loader.start(function (t) {
    if (t === 1 && module.useMouseAudio) {
    }
    callback(t)
  })
}

function loadAssets(callback) {
  lights.loadAssets()
  particles.loadAssets()

  state.loader.start(function (t) {
    if (t === 1) {
      if (!_scene) return;

      particles.init()
      _scene.add(particles.mesh)
      lights.postInit()
      
      state.isReady = true
    }
    callback(t)
  })
}

function setMousePos(x, y) {
  state.mouse2d.set(x, y, 0)
}

function main() {
  THREE.ShaderChunk.position_packing = glslify(require('!raw-loader!./position_packing.glsl')).replace(/#define\sGLSLIFY\s./, '')
  THREE.ShaderChunk.InScatter = glslify(require('!raw-loader!./InScatter.glsl')).replace(/#define\sGLSLIFY\s./, '')
  
  const renderSettings = merge({
    canvas: module.canvas,
    context: state.gl,
    antialias: !module.usePostprocessing,
    alpha: false
  }, module.rendererSettings)
  
  _renderer = state.renderer = new THREE.WebGLRenderer(renderSettings)
  
  _scene = state.scene = new THREE.Scene
  _scene.name = 'particles-animation'
  
  fboHelper.init(_renderer, module.renderTargetFloatType)
  
  _camera = state.camera = new THREE.PerspectiveCamera(90, 1, 0.02, 100)
  _scene.add(_camera)
  __camera = state.actualCamera = _camera.clone()
  _scene.add(__camera)

  controls.init()
  
  pointCloudConfiguration.init()

  state.resolution = new THREE.Vector2

  postprocessing.init({
    renderer: _renderer,
    scene: _scene,
    camera: __camera
  })
  
  /*
  const geometry = new THREE.BoxGeometry( 0.5, 0.5, 0.5 );
  for ( let i = 0; i < geometry.faces.length; i += 2 ) {
    let hex = Math.random() * 0xffffff;
    geometry.faces[ i ].color.setHex( hex );
    geometry.faces[ i + 1 ].color.setHex( hex );
  }
  const material = new THREE.MeshBasicMaterial( { vertexColors: THREE.FaceColors, overdraw: 0.5 } )
  const cube = new THREE.Mesh( geometry, material );
  cube.position.y = 1;
  cube.position.z = -0.25;
  _scene.add(cube)
  */
}

function resize(_width, _height) {
  if (module.hasInitialized) {
    if (_width && _height) {
      y = state.width = _width
      w = state.height = _height
    }

    const n = module.allowRetina && browser.isRetina
    const width = y * (n ? 2 : 1)
    const height = w * (n ? 2 : 1)

    state.resolution.set(width, height)
    
    postprocessing.resize(width, height)
    lights.resize(width, height)

    _renderer.domElement.style.width = y + 'px'
    _renderer.domElement.style.height = w + 'px'
  }
}

function reset() {
  state.isReset = true
  state.transitionRenderCountRatio = 1
  state.animation = 0

  transitionParticle.reset()
  p.reset()
}

function render(date) {
  if (!_renderer) return;

  _renderer.render(_scene, _camera)
  
  if (module.hasInitialized) {
    pointCloudConfiguration.update(date)
    controls.update(date)
    _scene.updateMatrixWorld()
    transitionParticle.update(date)
    p.update(date)
    lights.update(date)
    ambientParticle.update(date)
    v.update(date)
    particles.update(date)
    postprocessing.render(date)

    if (!module.usePostprocessing) {
      const state = fboHelper.getColorState()
      _renderer.autoClearColor = false
      _renderer.autoClearStencil = false
      _renderer.autoClearDepth = false

      const material = fboHelper.colorMaterial
      material.blending = THREE.CustomBlending
      material.blendEquation = THREE.AddEquation
      material.blendDst = THREE.OneFactor
      material.blendSrc = THREE.ZeroFactor
      material.blendEquationAlpha = THREE.AddEquation
      material.blendDstAlpha = THREE.ZeroFactor
      material.blendSrcAlpha = THREE.OneFactor
      material.transparent = true
      fboHelper.renderColor(0, 1)
      material.blending = THREE.NoBlending
      material.transparent = false
      fboHelper.setColorState(state)
    }
    state.isReset = false
  }
}

if (typeof window !== 'undefined') {
  window[core.globalId] = module
}

module.checkIsSupported = checkIsSupported
module.init = init
module.loadBasicAssets = loadBasicAssets
module.loadAssets = loadAssets
module.setMousePos = setMousePos
module.resize = resize
module.reset = reset
module.render = render
module.setModel = particles.setModel

export default module


class Stage {
  constructor () {
    this.init()
  }
  
  init() {
  
  }
  
  initScene () {
  
  }
  
  initCamera () {
  
  }
  
  initRenderer () {
  
  }
  
  initPost () {
  
  }
  
  resize () {
  
  }
  
  update () {
  
  }
  
  render () {
  
  }
}


