import React, {Component} from 'react'
import MediaQuery from 'react-responsive'

const Logo = (props) => (
  <a className="navbar-brand mb-0 h1 logo-like shrink-animation" onClick={() => props.changeFullpageSlide(0)}
     id="logo">
    <span className="logo-shrinkable"><span className="first-letter">H</span>umante</span><span
    className="last-letter">q</span>
  </a>
)

const NavItem = (props) => (
  <li className={`nav-item ${props.active === props.index ? 'active' : ''}`}
      onClick={() => props.changeFullpageSlide(props.index)}>
    <a className="nav-link">{props.children}</a>
  </li>
)

class NavBar extends Component {
  state = {
    isMenuOpened: false
  }
  
  onTogglerClick = () => {
    this.setState({isMenuOpened: !this.state.isMenuOpened})
  }
  
  onNavItemClick = (index) => {
    const {changeFullpageSlide} = this.props
    changeFullpageSlide && changeFullpageSlide(index)
    this.setState({isMenuOpened: false})
  }
  
  render() {
    const {props} = this
    const {isMenuOpened} = this.state
    
    return (
      <header className="navbar navbar-expand navbar-dark flex-column flex-md-row top-bar hq-navbar">
        <div className="navbar-container row grid-x grid-margin-x">
        <div className="title-bar">
          <button className={`navbar-toggler menu-icon ${isMenuOpened ? 'menu-icon-close' : ''}`} type="button"
                  data-toggle="collapse"
                  data-target="#navbarSupportedContent"
                  aria-controls="navbarSupportedContent"
                  aria-expanded="false"
                  aria-label="Toggle navigation"
                  onClick={this.onTogglerClick}>
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="title-bar-title">
            <Logo {...props}/>
          </div>
        </div>
        
        <nav className={`flex-row ml-md-auto d-none d-md-flex top-bar-right ${isMenuOpened ? 'visible' : ''}`}
             id="navbarSupportedContent"
             role="navigation">
          <ul className="navbar-nav mr-auto menu"
              style={{display: 'flex', flexDirection: 'row', justifyContent: 'flex-end'}}>
            <NavItem index={1}
                     active={props.active}
                     changeFullpageSlide={this.onNavItemClick}>Mission</NavItem>
            <NavItem index={2}
                     active={props.active}
                     changeFullpageSlide={this.onNavItemClick}>Context</NavItem>
            <NavItem index={3}
                     active={props.active}
                     changeFullpageSlide={this.onNavItemClick}>Science</NavItem>
            <NavItem index={4}
                     active={props.active}
                     changeFullpageSlide={this.onNavItemClick}>Projects</NavItem>
            <NavItem index={5}
                     active={props.active}
                     changeFullpageSlide={this.onNavItemClick}>Team</NavItem>
            <NavItem index={6}
                     active={props.active}
                     changeFullpageSlide={this.onNavItemClick}>Contact</NavItem>
          </ul>
        </nav>
        </div>
      </header>
    )
  }
}

export default NavBar



