/**
 * @see https://github.com/input-output-hk/symphony/blob/b950f20bc62c167cbaf6b92bf17e16383180e590/static/assets/js/intro/expl/src/js/postprocessing/unrealBloom/unrealBloom.js
 */

/* eslint import/no-webpack-loader-syntax: off */

import * as THREE from 'three'
import glslify from 'glslify'
import effectComposer from '../EffectComposer'
import fboHelper from '../../fboHelper'
import PostEffect from '../PostEffect'


let _nMips
let _directionX
let _directionY
let _lenFlareRenderTarget
let _blurMaterials = []
let _renderTargetsHorizontal = []
let _renderTargetsVertical = []
let _bloomTintColors = []

class UnrealBloomEffect extends PostEffect {
  maskActive = false
  amount = 2
  radius = 0
  threshold = 0
  
  lenFlareOpacity = .1
  lenFlareFeatureMaterial = null

  init() {
    _nMips = 5 // fix passes for now
    const renderTargetType = THREE.UnsignedByteType
    _lenFlareRenderTarget = fboHelper.createRenderTarget(1, 1, THREE.RGBFormat, renderTargetType)
    for (let i = 0; i < _nMips; i++) {
      _renderTargetsHorizontal.push(fboHelper.createRenderTarget(1, 1, THREE.RGBFormat, renderTargetType))
      _renderTargetsVertical.push(fboHelper.createRenderTarget(1, 1, THREE.RGBFormat, renderTargetType))
      _bloomTintColors.push(new THREE.Vector3(1, 1, 1))
    }
    _directionX = new THREE.Vector2(1, 0)
    _directionY = new THREE.Vector2(0, 1)
    
    super.init.call(this, {
      uniforms: {
        u_blurTexture1: {type: 't', value: _renderTargetsVertical[0].texture},
        u_blurTexture2: {type: 't', value: _renderTargetsVertical[1].texture},
        u_blurTexture3: {type: 't', value: _renderTargetsVertical[2].texture},
        u_blurTexture4: {type: 't', value: _renderTargetsVertical[3].texture},
        u_blurTexture5: {type: 't', value: _renderTargetsVertical[4].texture},
        u_bloomStrength: {type: 'f', value: 1.0},
        u_bloomFactors: {type: 'f', value: [1.0, 0.8, 0.6, 0.4, 0.2]},
        u_bloomTintColors: {type: 'v3', value: _bloomTintColors},
        u_bloomRadius: {value: 0.0}
      },
      defines: {
        NUM_MIPS: _nMips
      },
      fragmentShader: glslify(require('!raw-loader!./glsl/unrealBloom.frag'))
    })
    
    this.lenFlareFeatureMaterial = new THREE.RawShaderMaterial({
      uniforms: {
        u_texture: {type: 't', value: null},
        u_resolution: {type: 'v2', value: new THREE.Vector2},
        u_aspect: {type: 'v2', value: new THREE.Vector2(1, 1)},
        u_ghostDispersal: {type: 'f', value: .3},
        u_haloWidth: {type: 'f', value: .6},
        u_distortion: {type: 'f', value: 3},
        u_opacity: {type: 'f', value: .1},
        u_luminosityThreshold: {type: 'f', value: 1},
        u_smoothWidth: {type: 'f', value: 1}
      },
      vertexShader: fboHelper.vertexShader,
      fragmentShader: fboHelper.precisionPrefix + glslify(require('!raw-loader!./glsl/lenFlareFeature.frag')),
      blending: THREE.NoBlending
    })
    
    const kernelSizeArray = [3, 5, 7, 9, 11]
    
    for (let i = 0; i < _nMips; i++) {
      var kernelRadius = kernelSizeArray[i]
      _blurMaterials[i] = new THREE.RawShaderMaterial({
        uniforms: {
          u_texture: {type: 't', value: null},
          u_texSize: {type: 'v2', value: new THREE.Vector2},
          u_direction: {type: 'v2', value: null}
        },
        vertexShader: fboHelper.vertexShader,
        fragmentShader: fboHelper.precisionPrefix + glslify(require('!raw-loader!./glsl/unrealBloomBlur.frag')),
        blending: THREE.NoBlending,
        defines: {
          KERNEL_RADIUS: kernelRadius,
          SIGMA: kernelRadius
        }
      })
    }
  }
  
  resize(width, height) {
    let resx = Math.round(width / 2)
    let resy = Math.round(height / 2)
    
    fboHelper.resizeRenderTarget(_lenFlareRenderTarget, resx, resy)
    
    for (let i = 0; i < _nMips; i++) {
      fboHelper.resizeRenderTarget(_renderTargetsHorizontal[i], resx, resy)
      fboHelper.resizeRenderTarget(_renderTargetsVertical[i], resx, resy)
      _blurMaterials[i].uniforms.u_texSize.value = new THREE.Vector2(resx, resy)
      resx = Math.round(resx / 2)
      resy = Math.round(resy / 2)
    }
  }
  
  needsRender() {
    return !!this.amount
  }
  
  render(date, renderTarget, toScreen) {
    const state = fboHelper.getColorState()

    const renderer = fboHelper.renderer
    renderer.autoClear = true
    renderer.setClearColor(0, 0)

    if (this.maskActive) renderer.context.disable(renderer.context.STENCIL_TEST)
  
    // 1. Extract Bright Areas
    this.lenFlareFeatureMaterial.uniforms.u_texture.value = renderTarget.texture
    this.lenFlareFeatureMaterial.uniforms.u_luminosityThreshold.value = this.threshold
    this.lenFlareFeatureMaterial.uniforms.u_resolution.value.set(renderTarget.width, renderTarget.height)
    this.lenFlareFeatureMaterial.uniforms.u_aspect.value.init = renderTarget.width / renderTarget.height
    this.lenFlareFeatureMaterial.uniforms.u_opacity.value = this.lenFlareOpacity
    
    fboHelper.render(this.lenFlareFeatureMaterial, _lenFlareRenderTarget)
  
    // // 2. Blur All the mips progressively
    var inputRenderTarget = _lenFlareRenderTarget
    
    for (let i = 0; i < _nMips; i++) {
      let blurMaterial = _blurMaterials[i]
      blurMaterial.uniforms.u_texture.value = inputRenderTarget.texture
      blurMaterial.uniforms.u_direction.value = _directionX
      fboHelper.render(blurMaterial, _renderTargetsHorizontal[i], true)
      blurMaterial.uniforms.u_texture.value = _renderTargetsHorizontal[i].texture
      blurMaterial.uniforms.u_direction.value = _directionY
      fboHelper.render(blurMaterial, _renderTargetsVertical[i], true)
      inputRenderTarget = _renderTargetsVertical[i]
    }

    const material = this.material
    material.uniforms.u_texture.value = renderTarget.texture
    material.uniforms.u_bloomStrength.value = this.amount
    material.uniforms.u_bloomRadius.value = this.radius
    renderTarget = effectComposer.render(material, toScreen)
    
    if (this.maskActive) renderer.context.enable(renderer.context.STENCIL_TEST)
    
    fboHelper.setColorState(state)
    
    window._lenFlareRenderTarget = _lenFlareRenderTarget
    
    return renderTarget
  }
}

export default new UnrealBloomEffect


