/**
 * @see https://github.com/input-output-hk/symphony/blob/b950f20bc62c167cbaf6b92bf17e16383180e590/static/assets/js/intro/expl/src/js/postprocessing/PostEffect.js
 */

/* eslint import/no-webpack-loader-syntax: off */

import * as THREE from 'three'
import glslify from 'glslify'
import effectComposer from './EffectComposer'
import fboHelper from '../fboHelper'
import merge from 'mout/object/merge'

let undef

const DEFAULT_QUAD_VERTEX_SHADER = glslify(require('!raw-loader!../../glsl/shaderMaterialQuad.vert'))

export default class PostEffect {
  uniforms
  isRawMaterial
  addRawShaderPrefix
  defines
  
  init(config) {
    merge(this, {
      uniforms: {
        u_texture: {type: 't', value: undef},
        u_resolution: {type: 'v2', value: effectComposer.resolution}
      },
      defines: {},
      enabled: true,
      vertexShader: '',
      fragmentShader: '',
      isRawMaterial: true,
      addRawShaderPrefix: true
    }, config)
    
    if (!this.vertexShader) {
      this.vertexShader = this.isRawMaterial ? fboHelper.vertexShader : DEFAULT_QUAD_VERTEX_SHADER
    }
    
    if (this.addRawShaderPrefix && this.isRawMaterial) {
      this.vertexShader = fboHelper.precisionPrefix + this.vertexShader
      this.fragmentShader = fboHelper.precisionPrefix + this.fragmentShader
    }
    
    this.material = new (this.isRawMaterial ? THREE.RawShaderMaterial : THREE.ShaderMaterial)({
      uniforms: this.uniforms,
      vertexShader: this.vertexShader,
      fragmentShader: this.fragmentShader,
      defines: this.defines
    })
  }
  
  resize(width, height) {
  }
  
  /*
    `needsRender` is a dynamic way to skip the rendering.
    For example, if blurRadius is zero, there is no point to render
  */
  needsRender(date) {
    return true
  }
  
  render(date, texture, toScreen) {
    this.uniforms.u_texture.value = texture.texture
    return effectComposer.render(this.material, toScreen)
  }
}



