import React, {Component} from 'react'
import Stats from 'stats.js'
import VisibilitySensor from 'react-visibility-sensor'
import * as THREE from 'three'
import TweenLite from 'gsap/TweenLite'

import scene from './scene'


let prevTime = 0

class Scene3D extends Component {
  componentDidMount() {
    // this.initStats()
    
    const width = this.mount.clientWidth || window.innerWidth
    const height = this.mount.clientHeight || window.innerHeight
    
    this.width = width
    this.height = height
    
    this.canvas = document.createElement('canvas')
    this.canvas.width = width
    this.canvas.height = height
    this.mount.appendChild(this.canvas)

    this.scene = scene

    window.scene = this.scene
    
    scene.init({canvas: this.canvas})
    scene.loadBasicAssets(e => {
      if (e === 1) {
        this.scene.loadAssets((e) => {
          if (e === 1) {
            if (this.continueAnimationOnReady !== undefined) {
              this.playAnimation (this.continueAnimationOnReady)
              this.continueAnimationOnReady = undefined
            }
          }
        })
        
        requestAnimationFrame(this.start)

        // this.initGUI()
      }
    })

    window.addEventListener('mousemove', this.onMouseMove)
    window.addEventListener('resize', this.onResize)

    this.onResize()

    this.temp_object = { progress: 0 }

    this.componentWillReceiveProps(this.props)
  }

  componentWillReceiveProps(nextProps) {

    const {TweenLite} = require('gsap/TweenLite')

    if (nextProps.backgroundColor) {

      if (!this.scene.properties.renderer) return;

      this.color_from = this.scene.properties.renderer.getClearColor();
      this.color_to   = new THREE.Color; this.color_to.setStyle(nextProps.backgroundColor);

      if (this.color_from.r + this.color_from.g + this.color_from.b == 0) {
        // if renderer background color is not set, apply new color immediately
        this.color_from.copy (this.color_to);
      }

      this.temp_object.progress = 0
      TweenLite.killTweensOf(this.temp_object)

      let onUpdate = () => {

          // set the background and particles color

          let color = this.color_from.lerp (this.color_to, this.temp_object.progress)
          let hsl = color.getHSL()

          this.scene.properties.renderer.setClearColor(color, 1)

		  color = new THREE.Color
		  color.setHSL(hsl.h, hsl.s, Math.min (hsl.l,
			// tone the color down if near white
			1 - 0.06 * (color.r * color.r + color.g * color.g + color.b * color.b)
		  ));

          this.scene.properties.transitionParticleColorHex = color.getHex()

		  // 3D model point cloud colors

		  color.setHSL(hsl.h, hsl.s, hsl.l * 0.8);
		  this.scene.properties.pointCloudColor1Hex = color.getHex()

		  color.setHSL(hsl.h, hsl.s, hsl.l * 0.8 + 0.2);
		  this.scene.properties.pointCloudColor2Hex = color.getHex()
	  }

      onUpdate()

      TweenLite.to(this.temp_object, 2, {
        ease: 'linear',
        progress: 1,
        onUpdate: onUpdate
      })

    }

    if (nextProps.activeSlide !== undefined) {

      // show the model on certain slides

      let animation = 0, model

      switch(nextProps.activeSlide) {
        case 1: // Mission
          animation = 1
          model = 0
          break
        case 2: // Context
          animation = 1
          model = 1
          break
        case 6: // Contacts
          animation = 1
          model = 2
          break
      }

      scene.setModel (model)
      this.playAnimation (animation)
    }
  }

  playAnimation = function(to) {
    if (to === undefined) to = 1;
    if (this.scene.properties.isReady) {
      TweenLite.to(this.scene.properties, 5, {
        animation: to
      })
    } else {
      this.continueAnimationOnReady = to
    }
  }

  
  onMouseMove = (e) => {
    const i = this.canvas.getBoundingClientRect()
    let t = (e.clientX - i.left) / i.width * 2 - 1
    let o = 1 - (e.clientY - i.top) / i.height * 2
    this.scene.setMousePos(t, o)
  }
  
  onResize = () => {
    const e = document.body.getBoundingClientRect()
    this.scene.resize(e.width, window.innerHeight)
  }
  
  start = () => {
    if (!this.frameId) {
      this.frameId = requestAnimationFrame(this.animate)
    }
  }
  
  stop = () => {
    cancelAnimationFrame(this.frameId)
    this.frameId = null
  }
  
  animate = (e) => {
    this.frameId = window.requestAnimationFrame(this.animate)
    
    // this.stats.begin()
    
    const time = (e - prevTime) / 1e3
    prevTime = e
    
    this.scene.render(time)
    
    // this.stats.end()
  }
  
  onVisibleChange = (isVisible) => {
    if (isVisible) {
      this.start()
    } else {
      this.stop()
    }
  }
  
  initStats() {
    this.stats = new Stats()
    
    this.stats.showPanel(0) // 0: fps, 1: ms, 2: mb, 3+: custom
    this.stats.dom.style = 'position: absolute; right: 0; bottom: 0;'
    this.mount.appendChild(this.stats.dom)
  }
  
  initGUI() {
    const {default: dat} = require('dat.gui')
    this.gui = new dat.GUI({name: 'Scene3D', autoPlace: false})
    // this.gui.domElement.parentNode.classList.add('hidden')
    
    const camera = this.gui.addFolder('Camera')
    camera.add(this.scene.properties, 'cameraAzimuthAngle', 0, 2 * Math.PI, 1e-4).name('azimuth angle').listen()
    camera.add(this.scene.properties, 'cameraAdditionalPolarAngle', 0, 2 * Math.PI, 1e-4).name('polar angle').listen()
    camera.add(this.scene.properties, 'cameraAdditionalMovementScale', 0, 1, 1e-4).name('movement scale').listen()
    camera.add(this.scene.properties.camera.position, 'x', -5, 5, 1e-4).name('camera X').listen()
    camera.add(this.scene.properties.camera.position, 'y', -5, 5, 1e-4).name('camera Y').listen()
    camera.add(this.scene.properties.camera.position, 'z', -5, 5, 1e-4).name('camera Z').listen()
    camera.add(this.scene.properties.actualCamera.position, 'x', -5, 5, 1e-4).name('actualCamera X').listen()
    camera.add(this.scene.properties.actualCamera.position, 'y', -5, 5, 1e-4).name('actualCamera Y').listen()
    camera.add(this.scene.properties.actualCamera.position, 'z', -5, 5, 1e-4).name('actualCamera Z').listen()
    camera.add(this.scene.properties.controls.target, 'x', -5, 5, 1e-4).name('target X').listen()
    camera.add(this.scene.properties.controls.target, 'y', -5, 5, 1e-4).name('target Y').listen()
    camera.add(this.scene.properties.controls.target, 'z', -5, 5, 1e-4).name('target Z').listen()
    camera.open()
    
    const light = this.gui.addFolder('Light')
    light.add(this.scene.properties.pointLightPos, 'x', -3, 3, 1e-4).name('X').listen()
    light.add(this.scene.properties.pointLightPos, 'y', -3, 5, 1e-4).name('Y').listen()
    light.add(this.scene.properties.pointLightPos, 'z', -3, 3, 1e-4).name('Z').listen()
    light.addColor(this.scene.properties, 'pointLightColorHex').name('color').listen().listen()
    light.add(this.scene.properties, 'pointLightScatterDivider', 1, 500, 1e-4).name('scatter divider').listen()
    light.add(this.scene.properties, 'pointLightScatterPowInv', .1, 3, 1e-4).name('scatter pow inv').listen()
    light.open()
    
    const cloud = this.gui.addFolder('Point Cloud')
    cloud.add(this.scene.properties, 'pointCloudLightDistance', .1, 20, 1e-4).name('direct distance').listen()
    cloud.addColor(this.scene.properties, 'pointCloudColor1Hex').name('color1').listen().listen()
    cloud.addColor(this.scene.properties, 'pointCloudColor2Hex').name('color2').listen().listen()
    cloud.add(this.scene.properties, 'pointCloudDiffMultiplier', .1, 8, 1e-4).name('diff multiplier').listen()
    cloud.add(this.scene.properties, 'pointCloudSpecMultiplier', .1, 4, 1e-4).name('spec multiplier').listen()
    cloud.add(this.scene.properties, 'pointCloudParticleSize', .1, 4, 1e-4).name('size').listen()
    
    const transitionParticle = this.gui.addFolder('Transition Particle')
    transitionParticle.add(this.scene.properties, 'transitionParticleLightDistance', .1, 20, 1e-4).name('direct distance').listen()
    transitionParticle.addColor(this.scene.properties, 'transitionParticleColorHex').name('color').listen().listen()
    transitionParticle.add(this.scene.properties, 'transitionParticleDiffMultiplier', .1, 8, 1e-4).name('diff multiplier').listen()
    transitionParticle.add(this.scene.properties, 'transitionParticleSpecMultiplier', .1, 4, 1e-4).name('spec multiplier').listen()
    transitionParticle.add(this.scene.properties, 'transitionParticleSize', .1, 4, 1e-4).name('size').listen()
    
    const ambientParticle = this.gui.addFolder('Ambient Particle')
    ambientParticle.add(this.scene.properties, 'ambientSpecMultiplier', .1, 4, 1e-4).name('spec multiplier').listen()
    ambientParticle.add(this.scene.properties, 'ambientParticleSize', .1, 4, 1e-4).name('size').listen()
    
    const post = this.gui.addFolder('Post Processing')
    post.add(this.scene.properties, 'postRGBShift', 0, 5, 1e-4).name('RGBShift').listen()
    post.add(this.scene.properties, 'postBloomAmount', 0, 20, 1e-4).name('BloomAmount').listen()
    post.add(this.scene.properties, 'postBloomRadius', -1, 1, 1e-4).name('BloomRadius').listen()
    post.add(this.scene.properties, 'postBloomThreshold', 0, 1, 1e-4).name('BloomThreshold').listen()
    post.add(this.scene.properties, 'postBloomLenFlareOpacity', 0, 1, 1e-4).name('LenFlareOpacity').listen()
    
    const animation = this.gui.addFolder('Animation')
    let l = animation.add(this.scene.properties, 'animation', 0, 1, 1e-4).name('display animation').listen()
    l.domElement.style.opacity = '0.5'
    animation.add(this.scene.properties, 'animation', 0, 1, 1e-4).name('manual animation')
    animation.add(this.scene.properties, 'useAnimation').name('use animation')
    // animation.add(window, 'reset')
    // animation.add(window, 'playAnimation')
    animation.open()
    
    const grid = this.gui.addFolder('Grid')
    grid.add(this.scene.properties, 'showGrid').name('show')
    grid.add(this.scene.properties, 'gridOpacity', 0, 5, 1e-4).name('opacity').listen()
    grid.open()
    
    // const extras = this.gui.addFolder('Extras')
    // extras.open()
    
    document.body.appendChild(this.gui.domElement)
    this.gui.domElement.style.position = 'fixed'
  }
  
  render() {
    return (
      <VisibilitySensor onChange={this.onVisibleChange}>
        <div
          style={{width: '100%', height: '100%', minWidth: 400, minHeight: 400, position: 'fixed', left: 0, top: 0}}
          ref={(mount) => {
            this.mount = mount
          }}
        />
      </VisibilitySensor>
    )
  }
}

export default Scene3D



