/* eslint import/no-webpack-loader-syntax: off */

import * as THREE from 'three'
import glslify from 'glslify'
import PostEffect from '../PostEffect'


class RGBShift extends PostEffect {
  init() {
    super.init.call(this, {
      uniforms: {
        u_coverAspect: {value: new THREE.Vector2},
        u_rgbShiftIntensity: {value: .5}
      },
      fragmentShader: glslify(require('!raw-loader!./glsl/rgbShift.frag'))
    })
  }
  
  resize(width, height) {
    this.uniforms.u_coverAspect.value.set(width > height ? 1 : width / height, height > width ? 1 : height / width)
  }
}

export default new RGBShift



