/**
 * @author mpk / http://polko.me/
 *
 * WebGL port of Subpixel Morphological Antialiasing (SMAA) v2.8
 * Preset: SMAA 1x Medium (with color edge detection)
 * https://github.com/iryoku/smaa/releases/tag/v2.8
 */

/* eslint import/no-webpack-loader-syntax: off */

import * as THREE from 'three'
import glslify from 'glslify'
import fboHelper from '../../fboHelper'
import PostEffect from '../PostEffect'


let _edgesRenderTarget
let _weightsRenderTarget
let _uResolutionInv

function _createTexture(src) {
  const image = new Image
  image.src = src
  
  const texture = new THREE.Texture
  texture.image = image
  texture.generateMipmaps = false
  texture.needsUpdate = true
  texture.flipY = false

  return texture
}

function _getAreaTexture() {
  const texture = _createTexture('data:image/png;base64,')
  texture.format = THREE.RGBFormat
  texture.minFilter = THREE.LinearFilter
  
  return texture
}

function _getSearchTexture() {
  const texture = _createTexture('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAEIAAAAhCAAAAABIXyLAAAAAOElEQVRIx2NgGAWjYBSMglEwEICREYRgFBZBqDCSLA2MGPUIVQETE9iNUAqLR5gIeoQKRgwXjwAAGn4AtaFeYLEAAAAASUVORK5CYII=')
  texture.magFilter = THREE.NearestFilter
  texture.minFilter = THREE.NearestFilter
  
  return texture
}


class SMAAEffect extends PostEffect {
  materialEdges = null
  materialWeights = null
  
  init() {
    // render targets
    _edgesRenderTarget = fboHelper.createRenderTarget(1, 1)
    _weightsRenderTarget = fboHelper.createRenderTarget(1, 1, THREE.RGBAFormat)
    
    _uResolutionInv = {value: new THREE.Vector2}
  
    // materials - pass 1
    this.materialEdges = new THREE.RawShaderMaterial({
      uniforms: {
        u_texture: {value: null},
        u_resolutionInv: _uResolutionInv
      },
      vertexShader: fboHelper.precisionPrefix + glslify(require('!raw-loader!./glsl/SMAAEdgeDetectionVS.vert')),
      fragmentShader: fboHelper.precisionPrefix + glslify(require('!raw-loader!./glsl/SMAAColorEdgeDetectionPS.frag')),
      defines: {
        SMAA_THRESHOLD: '0.1'
      }
    })
  
    // materials - pass 2
    this.materialWeights = new THREE.RawShaderMaterial({
      uniforms: {
        u_edgesTexture: {type: 't', value: _edgesRenderTarget.texture},
        u_areaTexture: {type: 't', value: _getAreaTexture()},
        u_searchTexture: {type: 't', value: _getSearchTexture()},
        u_resolutionInv: _uResolutionInv
      },
      vertexShader: fboHelper.precisionPrefix  + glslify(require('!raw-loader!./glsl/SMAABlendingWeightCalculationVS.frag')),
      fragmentShader: fboHelper.precisionPrefix + glslify(require('!raw-loader!./glsl/SMAABlendingWeightCalculationPS.vert')),
      defines: {
        SMAA_MAX_SEARCH_STEPS: '8',
        SMAA_AREATEX_MAX_DISTANCE: '16',
        SMAA_AREATEX_PIXEL_SIZE: '( 1.0 / vec2( 160.0, 560.0 ) )',
        SMAA_AREATEX_SUBTEX_SIZE: '( 1.0 / 7.0 )'
      }
    })
  
    // materials - pass 3
    super.init.call(this, {
      uniforms: {u_weightsTexture: {value: _weightsRenderTarget.texture}, u_resolutionInv: _uResolutionInv},
      vertexShader: glslify(require('!raw-loader!./glsl/SMAANeighborhoodBlendingVS.vert')),
      fragmentShader: glslify(require('!raw-loader!./glsl/SMAANeighborhoodBlendingPS.frag'))
    })
  }
  
  resize(width, height) {
    fboHelper.resizeRenderTarget(_edgesRenderTarget, width, height)
    fboHelper.resizeRenderTarget(_weightsRenderTarget, width, height)
    _uResolutionInv.value.set(1 / width, 1 / height)
  }
  
  render(date, renderTarget, toScreen) {
    const state = fboHelper.getColorState()
    const renderer = fboHelper.renderer
    
    renderer.autoClear = true
    renderer.setClearColor(0, 0)
  
    // pass 1
    this.materialEdges.uniforms.u_texture.value = renderTarget.texture
    fboHelper.render(this.materialEdges, _edgesRenderTarget, true)
  
    // pass 2
    fboHelper.render(this.materialWeights, _weightsRenderTarget, true)
    
    // pass 3
    super.render.call(this, date, renderTarget, toScreen)
    
    fboHelper.setColorState(state)
  }
}

export default new SMAAEffect



