import * as THREE from 'three'

import configuration from './State'
import math from './helpers/math'
import ease from './utils/ease'

const module = {}

let camera
let controls
let someColor
let anotherColor
let useAnimation = -1

function init() {
  camera = configuration.camera
  controls = configuration.controls
  // Is it used?
  someColor = new THREE.Color
  // Is it used?
  anotherColor = new THREE.Color
}

function update() {
  if (!configuration.useAnimation) return
  
  const target = controls.target
  const animation = configuration.animation
  let animationFunction = ease.easeInQuad(animation)
  if (useAnimation !== 1 || useAnimation !== configuration.animation) {
    target.x = 0
    target.y = math.fit(animationFunction, 0, .2, 1.65, .65, ease.easeInOutQuad)
    target.z = 0.1
  }
  
  
  configuration.transitionParticleLightDistance = 5
  configuration.transitionParticleDiffMultiplier = math.fit(animation, 0, .5, 10, 4.6, ease.easeInOutSine)
  configuration.transitionParticleSpecMultiplier = math.fit(animation, 0, .5, .1175, .05, ease.easeInOutSine)
  configuration.transitionParticleSize = .7196

  configuration.pointCloudLightDistance = configuration.transitionParticleLightDistance
  configuration.pointCloudDiffMultiplier = math.fit(animation, 0, .5, 4.6, 10, ease.easeInOutSine)
  configuration.pointCloudSpecMultiplier = math.fit(animation, 0, .5, .05, .1175, ease.easeInOutSine)
  
  configuration.pointLightPos.y = 4.5
  configuration.pointLightScatterDivider = math.fit(animation, 0, .55, 8.7391, 357.4923, ease.easeInSine)
  configuration.pointLightScatterPowInv = math.fit(animation, 0, .55, 1.2644, .3537, ease.easeInSine)
  configuration.pointLightColorHex = 0x7f7f7f;
  configuration.pointCloudInitAnimation = math.fit(animation, .2, 1, 0, 1)
  
  configuration.cameraAzimuthAngleRatio = 1
  configuration.cameraShakePositionFrequency = 15e-5
  configuration.cameraShakeRotationFrequency = 2e-4
  configuration.cameraShakePositionAmplitude = .1
  configuration.cameraShakeRotationAmplitude = .1
  
  configuration.ambientSpecMultiplier = math.fit(animationFunction, 0, 1, 2.6122, 4, ease.easeInOutQuad)
  configuration.ambientParticleSize = math.fit(animationFunction, 0, 1, .5046, 1.3, ease.easeInOutQuad)
  
  configuration.mousePulseRadius = math.fit(animationFunction, 0, 1, .85, .5, ease.easeInOutQuad)
  
  configuration.postBloomAmount = math.fit(animationFunction, 0, 1, .2, 3.5, ease.easeInOutQuad)
  configuration.postBloomLenFlareOpacity = math.fit(animationFunction, 0, 1, .01, .03, ease.easeInOutQuad)
  
  configuration.gridOpacity = math.fit(animation, .8, 1, 0, 1)
  
  configuration.floorShadowOpacity = math.fit(animationFunction, .4, 1, 0, 2)
  
  configuration.transitionSimulatorEndAnimation = animation
  
  
  useAnimation = configuration.animation
}

module.init = init
module.update = update

export default module



