/**
 * @fileoverview fboHelper.js + extras
 */

/* eslint import/no-webpack-loader-syntax: off */

import * as THREE from 'three'
import glslify from 'glslify'

const module = {}

let renderer
let gl

let _scene
let _camera
let _mesh
let _color

let precisionPrefix = module.precisionPrefix = void 0
let renderTargetFloatType = module.renderTargetFloatType = void 0
let vertexShader = module.vertexShader = void 0
let MAX_VARYING_VECTORS = module.MAX_VARYING_VECTORS = void 0
let bigTriGeom = module.bigTriGeom = void 0
let colorMaterial = module.colorMaterial = void 0
let copyMaterial = module.copyMaterial = void 0
let multiColorMaterials = module.multiColorMaterials = []
let multiCopyMaterials = module.multiCopyMaterials = []

function init(refRenderer, refRenderTargetFloatType) {
  if (renderer) return
  
  module.renderer = renderer = refRenderer
  module.gl = gl = renderer.getContext()
  
  module.MAX_VARYING_VECTORS = MAX_VARYING_VECTORS = gl.getParameter(gl.MAX_VARYING_VECTORS)
  
  module.renderTargetFloatType = renderTargetFloatType = refRenderTargetFloatType === undefined ? THREE.FloatType : refRenderTargetFloatType
  
  module.precisionPrefix = precisionPrefix = `precision ${renderer.capabilities.precision} float;\n`
  
  _scene = new THREE.Scene
  _camera = new THREE.Camera
  _camera.position.z = 1
  
  module.vertexShader = vertexShader = precisionPrefix + glslify(require('!raw-loader!../glsl/quad.vert'))
  
  _color = new THREE.Color
  module.colorMaterial = colorMaterial = new THREE.RawShaderMaterial({
    uniforms: {
      u_color: {type: 'v4', value: new THREE.Vector4}
    },
    depthTest: false,
    depthWrite: false,
    blending: THREE.NoBlending,
    vertexShader: vertexShader,
    fragmentShader: precisionPrefix + glslify(require('!raw-loader!../glsl/quadColor.frag'))
  })
  
  module.copyMaterial = copyMaterial = new THREE.RawShaderMaterial({
    uniforms: {
      u_texture: {type: 't', value: undefined}
    },
    depthTest: false,
    depthWrite: false,
    blending: THREE.NoBlending,
    vertexShader: vertexShader,
    fragmentShader: precisionPrefix + glslify(require('!raw-loader!../glsl/quad.frag'))
  })
  
  module.bigTriGeom = bigTriGeom = new THREE.BufferGeometry
  bigTriGeom.addAttribute('position', new THREE.BufferAttribute(new Float32Array([-1, -1, 0, 4, -1, 0, -1, 4, 0]), 3))
  _mesh = new THREE.Mesh(bigTriGeom, copyMaterial)
  _mesh.frustumCulled = false
  _scene.add(_mesh)
}

function copy(texture, renderTarget, forceClear) {
  let material = copyMaterial
  const count = texture.length
  if (count && count > 1) {
    if (count < 2) console.error('please use copy() instead')
    material = multiCopyMaterials[count]
    if (!material) {
      material = new THREE.RawShaderMaterial({
        uniforms: {},
        depthTest: false,
        depthWrite: false,
        vertexShader: copyMaterial.vertexShader,
        fragmentShader: copyMaterial.fragmentShader,
        extensions: {
          drawBuffers: true
        },
        defines: {
          TEXTURE_COUNT: count
        }
      })
      for (let i = 0; i < count; i++) {
        material.uniforms['u_texture' + i] = {type: 't', value: undefined}
      }
    }
    for (let i = 0; i < count; i++) {
      material.uniforms['u_texture' + i].value = E(texture[i])
    }
  } else {
    copyMaterial.uniforms.u_texture.value = E(texture)
  }
  render(material, renderTarget, forceClear)
}

function renderColor(color, alpha, renderTarget, forceClear) {
  let material = colorMaterial
  if (color.length && color.length > 1) {
    const count = color.length
    if (count < 2) console.error('please use copy() instead')
    material = multiCopyMaterials[count]
    if (!material) {
      material = new THREE.RawShaderMaterial({
        uniforms: {},
        depthTest: false,
        depthWrite: false,
        vertexShader: copyMaterial.vertexShader,
        fragmentShader: copyMaterial.fragmentShader,
        extensions: {drawBuffers: true},
        defines: {TEXTURE_COUNT: count}
      })
      for (let i = 0; i < count; i++) {
        material.uniforms['u_color' + i] = {type: 'v4', value: new THREE.Vector4}
      }
    }
    for (let i = 0; i < count; i++) {
      _color.set(color[i])
      material.uniforms['u_color' + i].value.set(_color.r, _color.g, _color.b, alpha[i])
    }
  } else {
    _color.set(color)
    colorMaterial.uniforms.u_color.value.set(_color.r, _color.g, _color.b, alpha)
  }
  render(material, renderTarget, forceClear)
}

function renderGeometry(geometry, material, renderTarget, forceClear) {
  _mesh.geometry = geometry
  render(material, renderTarget, forceClear)
  _mesh.geometry = bigTriGeom
}

function renderObject(object, renderTartget, forceClear) {
  _mesh.visible = false
  _scene.add(object)
  if (renderTartget) {
    renderer.render(_scene, _camera, renderTartget, forceClear)
  } else {
    renderer.render(_scene, _camera, undefined, forceClear)
  }
  _scene.remove(object)
  _mesh.visible = true
}

function render(material, renderTarget, forceClear) {
  _mesh.material = material
  if (renderTarget) {
    renderer.render(_scene, _camera, renderTarget, forceClear)
  } else {
    renderer.render(_scene, _camera, undefined, forceClear)
  }
}

function resizeRenderTarget(renderTarget, width, height) {
  width = width | 0 || 1
  height = height | 0 || 1
  if (width !== renderTarget.width || height !== renderTarget.height) {
    renderTarget.setSize(width, height)
  }
}

function createRenderTarget(width, height, format, type, minFilter, magFilter) {
  const renderTarget = new THREE.WebGLRenderTarget(width || 1, height || 1, {
    format: format || THREE.RGBFormat,
    type: type || THREE.UnsignedByteType,
    minFilter: minFilter || THREE.LinearFilter,
    magFilter: magFilter || THREE.LinearFilter,
    depthBuffer: false,
    stencilBuffer: false
  })
  renderTarget.texture.generateMipMaps = false
  
  return renderTarget
}

function createMultiRenderTarget(count, width, height, format, type, minFilter, magFilter) {
  const T = THREE
  const renderTarget = new T.WebGLMultiRenderTarget(width || 1, height || 1, {
    format: format || THREE.RGBFormat,
    type: type || THREE.UnsignedByteType,
    minFilter: minFilter || THREE.LinearFilter,
    magFilter: magFilter || THREE.LinearFilter,
    depthBuffer: false,
    stencilBuffer: false
  })
  
  renderTarget.texture.generateMipMaps = false
  for (let i = 1; i < count; i++) {
    renderTarget.attachments.push(renderTarget.texture.clone())
  }
  
  return renderTarget
}

function getColorState() {
  return {
    autoClearColor: renderer.autoClearColor,
    autoClearStencil: renderer.autoClearStencil,
    autoClearDepth: renderer.autoClearDepth,
    clearColor: renderer.getClearColor().getHex(),
    clearAlpha: renderer.getClearAlpha()
  }
}

function setColorState(state) {
  renderer.setClearColor(state.clearColor, state.clearAlpha)
  renderer.autoClearColor = state.autoClearColor
  renderer.autoClearStencil = state.autoClearStencil
  renderer.autoClearDepth = state.autoClearDepth
}

function E(texture) {
  return texture ? texture.viewport ? texture.texture : texture : texture
}

module.init = init
module.copy = copy
module.renderColor = renderColor
module.renderGeometry = renderGeometry
module.renderObject = renderObject
module.render = render
module.resizeRenderTarget = resizeRenderTarget
module.createRenderTarget = createRenderTarget
module.createMultiRenderTarget = createMultiRenderTarget
module.getColorState = getColorState
module.setColorState = setColorState

export default module



