import * as THREE from 'three'

import configuration from '../../State'
import settings from '../../core/settings'
import effectComposer from './EffectComposer'
import merge from 'mout/object/merge'

import SMAAEffect from './smaa'
import Bloom from './unrealBloom'
import RGBShift from './rgb-shift'

const module = {}

module.vrRenderMethod = null
module.stereoEyesRenderMethod = null

function init(renderer) {
  effectComposer.init(merge({
    useDepthTexture: true,
    renderTargetFormat: THREE.RGBAFormat
  }, renderer))

  SMAAEffect.init()
  Bloom.init()
  RGBShift.init()

  if (settings.usePostprocessing) {
    effectComposer.queue.push(Bloom)
    effectComposer.queue.push(SMAAEffect)
    effectComposer.queue.push(RGBShift)
  }
}

function resize(e, t) {
  effectComposer.resize(e, t)

  // why if effect.resize never called implicitly ??
  for (let effect of effectComposer.queue) effect.resize(e, t)
}

function render(date, t) {
  RGBShift.uniforms.u_rgbShiftIntensity.value = configuration.postRGBShift
  Bloom.amount = configuration.postBloomAmount
  Bloom.radius = configuration.postBloomRadius
  Bloom.threshold = configuration.postBloomThreshold
  Bloom.lenFlareOpacity = configuration.postBloomLenFlareOpacity
  effectComposer.renderQueue(date, t)
}

module.init = init
module.resize = resize
module.render = render

export default module



