import parse from 'mout/queryString/parse'
import browser from '../helpers/browser'
import configuration from '../State'
import quality from '../quality'
import merge from 'mout/object/merge'

let module = {};

if (typeof window !== 'undefined') {
  module.query = parse(window.location.href)
} else {
  module.query = ''
}
module.globalId = 'pointCloudApi'
module.renderTargetFloatType = null
module.assetPath = './static/'
module.imgDir = ''
module.modelDir = ''
module.audioDir = ''
module.quality = module.quality !== undefined ? parseInt(module.quality, 10) : browser.isMobile ? 1 : 2
module.pointCloudFilename = ''
module.transitionParticleTextureWidth = 0
module.transitionParticleTextureHeight = 0
module.pointCloudCount = 0
module.particleScale = 1

module = merge(module, module.quality > 1 ? quality.high : quality.low)

module.transitionParticleCount = module.transitionParticleTextureWidth * module.transitionParticleTextureHeight
module.transitionRenderCountGrowDuration = 3
module.crossOrigins = []
module.useAudio = !browser.isMobile
module.useMouseAudio = module.useAudio && browser.WebAudioContext
module.cameraNoRotate = false
module.useFloatPacking = false
//module.usePostprocessing = module.quality > 1
module.usePostprocessing = false
module.allowRetina = browser.isMobile
module.ambientParticleCount = module.quality > 1 ? 1024 : 256
module.properties = configuration

export default module



