/* eslint import/no-webpack-loader-syntax: off */

import * as THREE from 'three'
import glslify from 'glslify'
import configuration from '../State'
import settings from '../core/settings'
import merge from 'mout/object/merge'

const module = {}

module.geometry = null
module.mesh = null

function init() {
  let ambientParticleCount = settings.ambientParticleCount

  var translation = new Float32Array(ambientParticleCount * 3)
  var rotationAxis = new Float32Array(ambientParticleCount * 3)
  var orientation = new Float32Array(ambientParticleCount * 4)
  var scale = new Float32Array(ambientParticleCount)
  var c = 0
  var d = 0
  var v = new THREE.Vector3
  var f = new THREE.Vector4
  
  for (let i = 0; i < ambientParticleCount; i++) {
    v.set((Math.random() - .5) * 8, Math.random() * .5 * 8, (Math.random() - .5) * 8)
    translation[c + 0] = v.x
    translation[c + 1] = v.y
    translation[c + 2] = v.z
    
    v.set(Math.random() - .5, Math.random() - .5, Math.random() - .5).normalize()
    rotationAxis[c + 0] = v.x
    rotationAxis[c + 1] = v.y
    rotationAxis[c + 2] = v.z
    
    f.set(Math.random() * 2 - 1, Math.random() * 2 - 1, Math.random() * 2 - 1, Math.random() * 2 - 1).normalize()
    orientation[d + 0] = f.x
    orientation[d + 1] = f.y
    orientation[d + 2] = f.z
    orientation[d + 3] = f.w
    
    scale[i] = 1 + Math.random() * 2
    c += 3
    d += 4
  }
  
  const instancedBufferGeometry = new THREE.InstancedBufferGeometry
  const planeBufferGeometry = new THREE.PlaneBufferGeometry(.005, .005)
  instancedBufferGeometry.addAttribute('position', planeBufferGeometry.attributes.position)
  instancedBufferGeometry.addAttribute('uv', planeBufferGeometry.attributes.uv)
  instancedBufferGeometry.addAttribute('a_translation', new THREE.InstancedBufferAttribute(translation, 3, 1))
  instancedBufferGeometry.addAttribute('a_scale', new THREE.InstancedBufferAttribute(scale, 1, 1))
  instancedBufferGeometry.addAttribute('a_rotationAxis', new THREE.InstancedBufferAttribute(rotationAxis, 3, 1))
  instancedBufferGeometry.addAttribute('a_orientation', new THREE.InstancedBufferAttribute(orientation, 4, 1))
  instancedBufferGeometry.index = planeBufferGeometry.index
  
  module.geometry = instancedBufferGeometry
  
  module.mesh = new THREE.Mesh(instancedBufferGeometry, new THREE.ShaderMaterial({
    uniforms: merge({
      u_time: {value: 0},
      u_lightDistance: {value: 1},
      u_specMultiplier: {value: 0},
      u_particleSize: {value: 1}
    }, configuration.pointLightUniforms),
    vertexShader: glslify(require('!raw-loader!./particle.vert')),
    fragmentShader: glslify(require('!raw-loader!./particle.frag')),
    blending: THREE.CustomBlending,
    blendEquation: THREE.AddEquation,
    blendDst: THREE.OneFactor,
    blendSrc: THREE.OneFactor,
    blendEquationAlpha: THREE.AddEquation,
    blendDstAlpha: THREE.OneFactor,
    blendSrcAlpha: THREE.OneFactor,
    side: THREE.DoubleSide,
    depthWrite: false,
    transparent: true
  }))
  module.mesh.name = 'ambient-particle'
  module.mesh.material.extensions.derivatives = true
  module.mesh.frustumCulled = false
  module.mesh.renderOrder = -1024
}

function update(date) {
  module.mesh.material.uniforms.u_time.value += date
  module.mesh.material.uniforms.u_lightDistance.value = configuration.ambientLightDistance
  module.mesh.material.uniforms.u_specMultiplier.value = configuration.ambientSpecMultiplier
  module.mesh.material.uniforms.u_particleSize.value = configuration.ambientParticleSize
}

module.init = init
module.update = update

export default module


