import * as THREE from 'three'

import configuration from './State'

const module = {}

module.init = init
module.update = update

let motion
let camera
let actualCamera
let target

function init() {
  camera = configuration.camera
  actualCamera = configuration.actualCamera
  camera.position.set(0.2, 2, 0.4)
  target = new THREE.Vector3(-.05, 1, -1)
  // target value is overridden in pointCloudConfiguration.js' update()
  configuration.controls = { target }
}

function update() {
  actualCamera.position.copy(camera.position)
  actualCamera.lookAt(target)
  actualCamera.updateMatrix()
  actualCamera.matrix.decompose(actualCamera.position, actualCamera.quaternion, actualCamera.scale)
}

export default module



