import * as THREE from 'three'
import glslify from 'glslify'

import configuration from './State'
import settings from './core/settings'
import fboHelper from './3d/fboHelper'
import a from './transitionParticle/transitionParticleModel'

const module = {}

module.currPositionRenderTarget = null
module.prevPositionRenderTarget = null

var textureWidth = module.textureWidth = 0
var textureHeight = module.textureHeight = 0
var defaultPositionTexture
var defaultPositionXYTexture
var defaultPositionZWTexture
var needsReset = false
var isReady = false
var p = void 0
var h = void 0
var x = void 0
var g = 0
var y = 0
var vertexData, pixelData, arrayXY, arrayZW

// init() is called from Scene3D/scene/particles.js when the mesh is loaded

function init(geometry) {
  vertexData = geometry.attributes.position.array
  g = geometry.attributes.position.count
  textureWidth = module.textureWidth = 1024
  textureHeight = module.textureHeight = Math.ceil(g / textureWidth)
  y = textureWidth * textureHeight
  updateVertexData()
  
  p = new THREE.RawShaderMaterial({
    uniforms: {
      u_textureSize: {value: new THREE.Vector2(textureWidth, textureHeight)},
      u_positionTexture: {value: null},
      u_mouse3d: {value: configuration.mouse3d},
      u_prevMouse3d: {value: configuration.prevMouse3d},
      u_mousePulseRadius: {value: 0},
      u_mouseVelocityForce: {value: 0},
      u_initAnimation: {value: 0},
      u_needsReset: {value: 0},
      u_time: {value: 0}
    },
    vertexShader: fboHelper.vertexShader,
    fragmentShader: fboHelper.precisionPrefix + require('!raw-loader!./55.frag'),
    blending: THREE.NoBlending,
    transparent: false,
    depthWrite: false,
    depthTest: false
  })
  
  if (!settings.useFloatPacking) {
    p.uniforms.u_defaultPositionTexture = {type: 't', value: defaultPositionTexture}
    module.prevPositionRenderTarget = fboHelper.createRenderTarget(textureWidth, textureHeight, THREE.RGBAFormat, fboHelper.renderTargetFloatType, THREE.NearestFilter, THREE.NearestFilter)
    module.currPositionRenderTarget = fboHelper.createRenderTarget(textureWidth, textureHeight, THREE.RGBAFormat, fboHelper.renderTargetFloatType, THREE.NearestFilter, THREE.NearestFilter)
  } else {
    h = new THREE.RawShaderMaterial({
      uniforms: {
        u_positionXYTexture: {type: 't', value: null},
        u_positionZWTexture: {type: 't', value: null},
        u_defaultPositionXYTexture: {type: 't', value: defaultPositionXYTexture},
        u_defaultPositionZWTexture: {type: 't', value: defaultPositionZWTexture},
        u_mouse3d: p.uniforms.u_mouse3d,
        u_prevMouse3d: p.uniforms.u_prevMouse3d,
        u_mousePulseRadius: p.uniforms.u_mousePulseRadius,
        u_mouseVelocityForce: p.uniforms.u_mouseVelocityForce,
        u_initAnimation: p.uniforms.u_initAnimation,
        u_needsReset: p.uniforms.u_needsReset,
        u_time: p.uniforms.u_time
      },
      vertexShader: p.vertexShader,
      fragmentShader: p.fragmentShader,
      blending: p.blending,
      transparent: p.transparent,
      depthWrite: p.depthWrite,
      depthTest: p.depthTest,
      defines: {
        USE_ENCODE: true,
        IS_XY: true
      }
    })
    
    x = new THREE.RawShaderMaterial({
      uniforms: {
        u_textureSize: p.uniforms.u_textureSize,
        u_positionXYTexture: h.uniforms.u_positionXYTexture,
        u_positionZWTexture: h.uniforms.u_positionZWTexture,
        u_defaultPositionXYTexture: h.uniforms.u_defaultPositionXYTexture,
        u_defaultPositionZWTexture: h.uniforms.u_defaultPositionZWTexture,
        u_mouse3d: p.uniforms.u_mouse3d,
        u_prevMouse3d: p.uniforms.u_prevMouse3d,
        u_mousePulseRadius: p.uniforms.u_mousePulseRadius,
        u_mouseVelocityForce: p.uniforms.u_mouseVelocityForce,
        u_initAnimation: p.uniforms.u_initAnimation,
        u_needsReset: p.uniforms.u_needsReset,
        u_time: p.uniforms.u_time
      },
      vertexShader: p.vertexShader,
      fragmentShader: p.fragmentShader,
      blending: p.blending,
      transparent: p.transparent,
      depthWrite: p.depthWrite,
      depthTest: p.depthTest,
      defines: {
        USE_ENCODE: true,
        IS_ZW: true
      }
    })
    module.currPositionXYRenderTarget = fboHelper.createRenderTarget(textureWidth, textureHeight, THREE.RGBAFormat, THREE.UnsignedByteType, THREE.NearestFilter, THREE.NearestFilter)
    module.prevPositionXYRenderTarget = fboHelper.createRenderTarget(textureWidth, textureHeight, THREE.RGBAFormat, THREE.UnsignedByteType, THREE.NearestFilter, THREE.NearestFilter)
    module.currPositionZWRenderTarget = fboHelper.createRenderTarget(textureWidth, textureHeight, THREE.RGBAFormat, THREE.UnsignedByteType, THREE.NearestFilter, THREE.NearestFilter)
    module.prevPositionZWRenderTarget = fboHelper.createRenderTarget(textureWidth, textureHeight, THREE.RGBAFormat, THREE.UnsignedByteType, THREE.NearestFilter, THREE.NearestFilter)
  }
  isReady = true
  reset()
}

function updateVertexData() {
  pixelData = pixelData || new Float32Array(y * 4)
  var n = 0
  var r = 0
  for (var i = 0; i < g; i++) {
    pixelData[r + 0] = vertexData[n + 0] // x goes to red
    pixelData[r + 1] = vertexData[n + 1] // y goes to green
    pixelData[r + 2] = vertexData[n + 2] // z goes to blue
    pixelData[r + 3] = 1                 // 1 goes to alpha
    n += 3
    r += 4
  }
  if (!settings.useFloatPacking) {
    defaultPositionTexture = defaultPositionTexture || new THREE.DataTexture(pixelData, textureWidth, textureHeight, THREE.RGBAFormat, THREE.FloatType)
    defaultPositionTexture.minFilter = THREE.NearestFilter
    defaultPositionTexture.magFilter = THREE.NearestFilter
    defaultPositionTexture.needsUpdate = true
  } else {
    arrayXY = arrayXY || new Uint8Array(y * 4)
    arrayZW = arrayZW || new Uint8Array(y * 4)
    b(pixelData, arrayXY, arrayZW)
    defaultPositionXYTexture = defaultPositionXYTexture || new THREE.DataTexture(arrayXY, textureWidth, textureHeight, THREE.RGBAFormat, THREE.UnsignedByteType)
    defaultPositionXYTexture.minFilter = THREE.NearestFilter
    defaultPositionXYTexture.magFilter = THREE.NearestFilter
    defaultPositionXYTexture.needsUpdate = true
    defaultPositionZWTexture = defaultPositionZWTexture || new THREE.DataTexture(arrayZW, textureWidth, textureHeight, THREE.RGBAFormat, THREE.UnsignedByteType)
    defaultPositionZWTexture.minFilter = THREE.NearestFilter
    defaultPositionZWTexture.magFilter = THREE.NearestFilter
    defaultPositionZWTexture.needsUpdate = true
  }
}

function b(e, t, n) {
  var r = void 0, o = void 0, i = void 0
  for (var a = 0, s = 0, u = e.length; a < u; a++) {
    i = e[s + 0] / 8 * .5 + .5
    o = i * 255 % 1
    r = i % 1 - o / 255
    t[s + 0] = r * 255
    t[s + 1] = o * 255
    i = e[s + 1] / 8 * .5 + .5
    o = i * 255 % 1
    r = i % 1 - o / 255
    t[s + 2] = r * 255
    t[s + 3] = o * 255
    i = e[s + 2] / 8 * .5 + .5
    o = i * 255 % 1
    r = i % 1 - o / 255
    n[s + 0] = r * 255
    n[s + 1] = o * 255
    n[s + 2] = 0
    n[s + 3] = 0
    s += 4
  }
}

// reset() may be called from Scene3D/scene/index.js

function reset() {
  needsReset = true
}

// update() is constantly called from Scene3D/scene/index.js

function update(date) {
  if (configuration.pointCloudInitAnimation === 0 || !isReady) return
  const state = fboHelper.getColorState()
  
  fboHelper.renderer.autoClearColor = false
  p.uniforms.u_initAnimation.value = configuration.pointCloudInitAnimation
  p.uniforms.u_mousePulseRadius.value = configuration.mousePulseRadius
  p.uniforms.u_mouseVelocityForce.value = configuration.mouseVelocityForce
  p.uniforms.u_needsReset.value = +needsReset
  p.uniforms.u_time.value += date
  if (!settings.useFloatPacking) {
    var s = module.prevPositionRenderTarget
    module.prevPositionRenderTarget = module.currPositionRenderTarget
    module.currPositionRenderTarget = s
    if (needsReset) {
      p.uniforms.u_positionTexture.value = defaultPositionTexture
    } else {
      p.uniforms.u_positionTexture.value = module.prevPositionRenderTarget.texture
    }
    fboHelper.render(p, module.currPositionRenderTarget)
  } else {
    var u = module.prevPositionXYRenderTarget
    module.prevPositionXYRenderTarget = module.currPositionXYRenderTarget
    module.currPositionXYRenderTarget = u
    u = module.prevPositionZWRenderTarget
    module.prevPositionZWRenderTarget = module.currPositionZWRenderTarget
    module.currPositionZWRenderTarget = u
    if (needsReset) {
      h.uniforms.u_positionXYTexture.value = a.currPositionXYRenderTarget.texture
      h.uniforms.u_positionZWTexture.value = a.currPositionZWRenderTarget.texture
    } else {
      h.uniforms.u_positionXYTexture.value = module.prevPositionXYRenderTarget.texture
      h.uniforms.u_positionZWTexture.value = module.prevPositionZWRenderTarget.texture
    }
    fboHelper.render(h, module.currPositionXYRenderTarget)
    fboHelper.render(x, module.currPositionZWRenderTarget)
  }
  fboHelper.setColorState(state)
  needsReset = false
}

module.init = init
module.reset = reset
module.update = update
module.updateVertexData = updateVertexData

export default module




