import * as THREE from 'three'

import configuration from '../State'
import settings from '../core/settings'
import transitionParticleModel from './transitionParticleModel'
import merge from 'mout/object/merge'

let module = {}

export let mesh = null

let u

function init() {
  var e = new THREE.BufferGeometry
  var t = settings.transitionParticleTextureWidth
  var i = settings.transitionParticleTextureHeight
  var l = t * i
  var c = new Float32Array(l * 2)
  var d = new Float32Array(l)
  for (var v = 0, f = 0; v < l; v++) {
    c[f + 0] = (v % t + .5) / t
    c[f + 1] = (~~(v / t) + .5) / i
    c[f + 2] = Math.random()
    d[v] = Math.random() * .5 + .5
    f += 2
  }
  e.addAttribute('position', new THREE.BufferAttribute(c, 3))
  e.addAttribute('a_pointSize', new THREE.BufferAttribute(d, 1))
  configuration.transitionParticleColor = new THREE.Color(configuration.transitionParticleColorHex)
  u = new THREE.ShaderMaterial({
    uniforms: merge({
      u_time: {value: 0},
      u_color: {value: configuration.transitionParticleColor},
      u_lightDistance: {value: 1},
      u_diffMultiplier: {value: 0},
      u_specMultiplier: {value: 0},
      u_viewportHeight: {value: 0},
      u_particleScale: {value: 1}
    }, configuration.pointLightUniforms),
    vertexShader: `#define GLSLIFY 1
attribute vec2 a_textureUv;

attribute float a_pointSize;

#ifdef USE_ENCODE
uniform sampler2D u_positionXYTexture;
uniform sampler2D u_positionZWTexture;
#else
uniform sampler2D u_positionTexture;
#endif

uniform float u_time;
uniform float u_viewportHeight;

uniform float u_particleScale;

varying vec3 v_worldPos;
varying vec3 v_viewPosition;
varying float v_halfPointSize;

//
// Description : Array and textureless GLSL 2D/3D/4D simplex
//               noise functions.
//      Author : Ian McEwan, Ashima Arts.
//  Maintainer : ijm
//     Lastmod : 20110822 (ijm)
//     License : Copyright (C) 2011 Ashima Arts. All rights reserved.
//               Distributed under the MIT License. See LICENSE file.
//               https://github.com/ashima/webgl-noise
//

vec4 mod289(vec4 x) {
  return x - floor(x * (1.0 / 289.0)) * 289.0; }

float mod289(float x) {
  return x - floor(x * (1.0 / 289.0)) * 289.0; }

vec4 permute(vec4 x) {
     return mod289(((x*34.0)+1.0)*x);
}

float permute(float x) {
     return mod289(((x*34.0)+1.0)*x);
}

vec4 taylorInvSqrt(vec4 r)
{
  return 1.79284291400159 - 0.85373472095314 * r;
}

float taylorInvSqrt(float r)
{
  return 1.79284291400159 - 0.85373472095314 * r;
}

vec4 grad4(float j, vec4 ip)
  {
  const vec4 ones = vec4(1.0, 1.0, 1.0, -1.0);
  vec4 p,s;

  p.xyz = floor( fract (vec3(j) * ip.xyz) * 7.0) * ip.z - 1.0;
  p.w = 1.5 - dot(abs(p.xyz), ones.xyz);
  s = vec4(lessThan(p, vec4(0.0)));
  p.xyz = p.xyz + (s.xyz*2.0 - 1.0) * s.www;

  return p;
  }

// (sqrt(5) - 1)/4 = F4, used once below
#define F4 0.309016994374947451

float snoise(vec4 v)
  {
  const vec4  C = vec4( 0.138196601125011,  // (5 - sqrt(5))/20  G4
                        0.276393202250021,  // 2 * G4
                        0.414589803375032,  // 3 * G4
                       -0.447213595499958); // -1 + 4 * G4

// First corner
  vec4 i  = floor(v + dot(v, vec4(F4)) );
  vec4 x0 = v -   i + dot(i, C.xxxx);

// Other corners

// Rank sorting originally contributed by Bill Licea-Kane, AMD (formerly ATI)
  vec4 i0;
  vec3 isX = step( x0.yzw, x0.xxx );
  vec3 isYZ = step( x0.zww, x0.yyz );
//  i0.x = dot( isX, vec3( 1.0 ) );
  i0.x = isX.x + isX.y + isX.z;
  i0.yzw = 1.0 - isX;
//  i0.y += dot( isYZ.xy, vec2( 1.0 ) );
  i0.y += isYZ.x + isYZ.y;
  i0.zw += 1.0 - isYZ.xy;
  i0.z += isYZ.z;
  i0.w += 1.0 - isYZ.z;

  // i0 now contains the unique values 0,1,2,3 in each channel
  vec4 i3 = clamp( i0, 0.0, 1.0 );
  vec4 i2 = clamp( i0-1.0, 0.0, 1.0 );
  vec4 i1 = clamp( i0-2.0, 0.0, 1.0 );

  //  x0 = x0 - 0.0 + 0.0 * C.xxxx
  //  x1 = x0 - i1  + 1.0 * C.xxxx
  //  x2 = x0 - i2  + 2.0 * C.xxxx
  //  x3 = x0 - i3  + 3.0 * C.xxxx
  //  x4 = x0 - 1.0 + 4.0 * C.xxxx
  vec4 x1 = x0 - i1 + C.xxxx;
  vec4 x2 = x0 - i2 + C.yyyy;
  vec4 x3 = x0 - i3 + C.zzzz;
  vec4 x4 = x0 + C.wwww;

// Permutations
  i = mod289(i);
  float j0 = permute( permute( permute( permute(i.w) + i.z) + i.y) + i.x);
  vec4 j1 = permute( permute( permute( permute (
             i.w + vec4(i1.w, i2.w, i3.w, 1.0 ))
           + i.z + vec4(i1.z, i2.z, i3.z, 1.0 ))
           + i.y + vec4(i1.y, i2.y, i3.y, 1.0 ))
           + i.x + vec4(i1.x, i2.x, i3.x, 1.0 ));

// Gradients: 7x7x6 points over a cube, mapped onto a 4-cross polytope
// 7*7*6 = 294, which is close to the ring size 17*17 = 289.
  vec4 ip = vec4(1.0/294.0, 1.0/49.0, 1.0/7.0, 0.0) ;

  vec4 p0 = grad4(j0,   ip);
  vec4 p1 = grad4(j1.x, ip);
  vec4 p2 = grad4(j1.y, ip);
  vec4 p3 = grad4(j1.z, ip);
  vec4 p4 = grad4(j1.w, ip);

// Normalise gradients
  vec4 norm = taylorInvSqrt(vec4(dot(p0,p0), dot(p1,p1), dot(p2, p2), dot(p3,p3)));
  p0 *= norm.x;
  p1 *= norm.y;
  p2 *= norm.z;
  p3 *= norm.w;
  p4 *= taylorInvSqrt(dot(p4,p4));

// Mix contributions from the five corners
  vec3 m0 = max(0.6 - vec3(dot(x0,x0), dot(x1,x1), dot(x2,x2)), 0.0);
  vec2 m1 = max(0.6 - vec2(dot(x3,x3), dot(x4,x4)            ), 0.0);
  m0 = m0 * m0;
  m1 = m1 * m1;
  return 49.0 * ( dot(m0*m0, vec3( dot( p0, x0 ), dot( p1, x1 ), dot( p2, x2 )))
               + dot(m1*m1, vec2( dot( p3, x3 ), dot( p4, x4 ) ) ) ) ;

  }

#include <position_packing>

void main () {

  #ifdef USE_ENCODE
    vec4 xy = texture2D( u_positionXYTexture, position.xy );
    vec4 zw = texture2D( u_positionZWTexture, position.xy );
    vec4 positionInfo = vec4(
      unpackPositionFloat(decodeRGFloat(xy.xy)),
      unpackPositionFloat(decodeRGFloat(xy.zw)),
      unpackPositionFloat(decodeRGFloat(zw.xy)),
      decodeRGFloat(zw.zw)
    );
  #else
    vec4 positionInfo = texture2D( u_positionTexture, position.xy );
  #endif

  vec3 pos = positionInfo.xyz;
  v_worldPos = (modelMatrix * vec4(pos, 1.0)).xyz;

  vec4 mvPosition = modelViewMatrix * vec4(pos, 1.0);
  float pointSize = (smoothstep(0.0, 0.2, positionInfo.a) * smoothstep(1.0, 0.8, positionInfo.a) * 15.0) / abs(mvPosition.z) * u_viewportHeight / 1080.0 * u_particleScale;
  // float pointSize = (a_pointSize * 15.0) / abs(mvPosition.z) * u_viewportHeight / 1080.0;

  v_viewPosition = -mvPosition.xyz;

  mvPosition.x += step(pointSize, 0.01) * 10000.0;

  gl_Position = projectionMatrix * mvPosition;
  pointSize = max(2.5, pointSize);
  gl_PointSize = pointSize;

  v_halfPointSize = pointSize * 0.5;
}
`,
    fragmentShader: `#define GLSLIFY 1
uniform mat4 modelViewMatrix;

uniform float u_lightDistance;
uniform float u_diffMultiplier;
uniform float u_specMultiplier;

uniform vec3 u_cameraPos;
uniform vec3 u_lightColor;
uniform vec3 u_lightPos;
uniform vec3 u_lightViewPos;

uniform vec3 u_color;

varying vec3 v_worldPos;
varying vec3 v_viewPosition;
varying float v_halfPointSize;

float clampRange (float minVal, float maxVal, float val) {
  return clamp((val - minVal) / (maxVal - minVal), 0.0, 1.0);
}

#include <InScatter>

void main () {
  float distToCenter = length(gl_PointCoord.xy - 0.5) * 2.0;
  float alpha = smoothstep(1.0, 0.9, distToCenter);
  if (alpha < 1.0) {
    discard;
  }
  vec3 N = vec3(gl_PointCoord.xy * 2.0 - 1.0, 0.0);
  N.z = sqrt(1.0 - N.x * N.x - N.y * N.y);
  N.y *= -1.0;
  N = normalize(N);

  vec3 viewGeometryPosition = -v_viewPosition;// + N * v_halfPointSize; // TODO: need the half point in view space

  vec3 LtoG = u_lightViewPos - viewGeometryPosition;
  vec3 nLtoG = normalize(LtoG);
  float distLtoG = length(LtoG);
  float dotNL = dot(N, nLtoG);
  // float diffuseFactor = 0.5 + smoothstep(-0.5, 1.0, dotNL) * 0.5;
  float diffuseFactor = smoothstep(-0.0, 1.0, nLtoG.z);//0.5 + smoothstep(-0.5, 1.0, dotNL) * 0.5;

  float lightDistanceWeight =  pow(clampRange(u_lightDistance, 0.0, distLtoG), 2.0);

  // vec3 color = u_color * mix(u_lightColor, vec3(1.0), 0.5) * (0.1 + diffuseFactor * 0.9) * lightDistanceWeight * u_diffMultiplier;
  vec3 color = u_color * mix(u_lightColor, vec3(1.0), 0.5) * max( diffuseFactor * lightDistanceWeight * u_diffMultiplier, 1.0);

  float specDotValue = dot(reflect(normalize(viewGeometryPosition), N), nLtoG);

  float spec = smoothstep(0.8, 0.82, specDotValue) * u_specMultiplier;// * smoothstep(0.2, 0.5, dotNL) *lightDistanceWeight * u_specMultiplier;
  color += spec * u_lightColor;

  color += color * u_lightColor * smoothstep(0.5, 0.3, distLtoG) * mix(4.0, 15.0, diffuseFactor);

  vec3 toCameraWorld = v_worldPos - u_cameraPos;
  vec3 nToCameraWorldDir = normalize(toCameraWorld);
  float toCameraDist = length(toCameraWorld);
  float scatter = InScatter(u_cameraPos, nToCameraWorldDir, u_lightPos, toCameraDist);
  color += scatter * u_lightColor;

  gl_FragColor = vec4(color, spec);
}
`,
    vertexColors: THREE.VertexColors,
    blending: THREE.NoBlending,
    transparent: false
  })

  module.mesh = new THREE.Points(e, u)
  if (!settings.useFloatPacking) {
    u.uniforms.u_positionTexture = {value: null}
  } else {
    u.defines.USE_ENCODE = true
    u.uniforms.u_positionXYTexture = {type: 't', value: null}
    u.uniforms.u_positionZWTexture = {type: 't', value: null}
  }
  module.mesh.onBeforeRender = function (e, t, n) {
    var o = e.getRenderTarget()
    u.uniforms.u_viewportHeight.value = o ? o.height : configuration.height
  }
  module.mesh.geometry.drawRange = {start: 0, count: 0}
  module.mesh.frustumCulled = false
}

function update(date) {
  if (!settings.useFloatPacking) {
    u.uniforms.u_positionTexture.value = transitionParticleModel.currPositionRenderTarget.texture
  } else {
    u.uniforms.u_positionXYTexture.value = transitionParticleModel.currPositionXYRenderTarget.texture
    u.uniforms.u_positionZWTexture.value = transitionParticleModel.currPositionZWRenderTarget.texture
  }
  u.uniforms.u_time.value += date
  u.uniforms.u_lightDistance.value = configuration.transitionParticleLightDistance
  u.uniforms.u_diffMultiplier.value = configuration.transitionParticleDiffMultiplier
  u.uniforms.u_specMultiplier.value = configuration.transitionParticleSpecMultiplier
  u.uniforms.u_particleScale.value = configuration.transitionParticleSize * settings.particleScale
  configuration.transitionParticleColor.setHex(configuration.transitionParticleColorHex)
  module.mesh.geometry.drawRange.count = ~~(configuration.transitionRenderCountRatio * settings.transitionParticleCount)
  module.mesh.visible = configuration.transitionSimulatorEndAnimation < 1
}

module.init = init
module.update = update

export default module



