/* eslint import/no-webpack-loader-syntax: off */

import * as THREE from 'three'
import glslify from 'glslify'

import configuration from './State'
import settings from './core/settings'
import OBJLoader from './helpers/OBJLoader'
import s from './55'
import merge from 'mout/object/merge'

import headPath from './models/Mission_Head.obj'
import pyrosPath from './models/Context_Pyros.obj'
import towerPath from './models/Contacts_Tower.obj'

const module = {}
const totalNumber = 23456;

module.isReady = false
module.loadAssets = loadAssets
module.init = init
module.update = update
module.geometry = null
module.mesh = null

let l
let c = true

let vertexData = []
module.setModel = function (i) {
  if (module.geometry) {
    if (vertexData[ i ]) {
		let j, k, names = ['position', 'normal'];
		for (j = 0; j < names.length; j++) {
			let attribute = module.geometry.getAttribute(names[ j ])
			let src = vertexData[ i ][ j ], dst = attribute.array, n = Math.min (src.length, dst.length)
			for (k = 0; k < n; k ++) {
				dst[ k ] = src[ k ];
			}
			attribute.needsUpdate = true
		}
      s.updateVertexData();
    }
  }
}

function allocateParticles (originalGeometry) {
  // allocate multiple particles along the edges to represent the model better
  let k, names = ['position', 'normal'], array = [];
  for (k = 0; k < names.length; k++) {
	  let originalArray = originalGeometry.getAttribute(names[ k ]).array; array[ k ] = [];

  let i, j, perFace = Math.ceil(totalNumber / (originalArray.length / 9));
  for(i = 0; i < originalArray.length; i += 9) {
    let ax = originalArray[i],     ay = originalArray[i + 1], az = originalArray[i + 2];
    let bx = originalArray[i + 3], by = originalArray[i + 4], bz = originalArray[i + 5];
    let cx = originalArray[i + 6], cy = originalArray[i + 7], cz = originalArray[i + 8];
    for (j = 0; j < perFace; j++) {
      if (array[ k ].length < 3 * totalNumber) {
        let a = Math.random(), b = Math.random(), c = Math.random(), d = j / perFace

        if (d < 1 / 3) {
          // ab
          b = 1 - a; c = 0
        } else if (d < 2 / 3) {
          // bc
          c = 1 - b; a = 0
        } else {
          // ca
          a = 1 - c; b = 0
        }

        array[ k ].push(
          a * ax + b * bx + c * cx,
          a * ay + b * by + c * cy,
          a * az + b * bz + c * cz
        );
      }
    }
  }
  }
  return array;
}

function loadAssets() {
  const loader = new OBJLoader

  const urls = [
    headPath,
    pyrosPath,
    towerPath
  ];

  for (let i = 0; i < urls.length; i++) {
    configuration.loader.add(urls[ i ], {
      type: 'any',
      weight: 10,
      hasLoading: true,
      loadFunc: function e(url, cb, loadingSignal) {
        loader.load(url, function (group) {
          if(!module.geometry) {
            module.geometry = new THREE.BufferGeometry;
            module.geometry.addAttribute('position', new THREE.BufferAttribute(new Float32Array(totalNumber * 3), 3));
            module.geometry.addAttribute('normal', new THREE.BufferAttribute(new Float32Array(totalNumber * 3), 3));
          }

          vertexData[ i ] = allocateParticles(group.children[0].geometry);

          cb()
        }, function (e) {
          if (e.lengthComputable) {
            loadingSignal.dispatch(e.loaded / e.total)
          }
        }, function (error) {
          console.log(error)
        })
      }
    })
  }
}

function init() {

  // s.init() creates the textures to hold vertex coordinates
  s.init(module.geometry)

  // this adds the attribute to map from a vertex to a pixel in said textures
  const width = s.textureWidth
  const height = s.textureHeight
  var a = module.geometry.attributes.position.count
  var c = new Float32Array(a * 2)
  for (var d = 0, v = 0; d < a; d++) {
    c[v + 0] = (d % width + .5) / width
    c[v + 1] = (~~(d / width) + .5) / height
    v += 2
  }
  module.geometry.addAttribute('a_textureUv', new THREE.BufferAttribute(c, 2))

  configuration.pointCloudColor1 = new THREE.Color(configuration.pointCloudColor1Hex)
  configuration.pointCloudColor2 = new THREE.Color(configuration.pointCloudColor2Hex)
  l = new THREE.ShaderMaterial({
    uniforms: merge({
      u_time: {value: 0},
      u_color1: {value: configuration.pointCloudColor1},
      u_color2: {value: configuration.pointCloudColor2},
      u_lightDistance: {value: 1},
      u_diffMultiplier: {value: 0},
      u_specMultiplier: {value: 0},
      u_initAnimation: {value: 0},
      u_viewportHeight: {value: 0},
      u_particleScale: {value: settings.particleScale}
    }, configuration.pointLightUniforms),
    vertexShader: glslify(require('!raw-loader!./glsl/particles.vert')),
    fragmentShader: glslify(require('!raw-loader!./glsl/particles.frag')),
    vertexColors: THREE.VertexColors,
    blending: THREE.NoBlending,
    transparent: false
  })

  if (!settings.useFloatPacking) {
    l.uniforms.u_positionTexture = {value: null}
  } else {
    l.defines.USE_ENCODE = true
    l.uniforms.u_positionXYTexture = {type: 't', value: null}
    l.uniforms.u_positionZWTexture = {type: 't', value: null}
  }
  module.mesh = new THREE.Points(module.geometry, l)
  module.mesh.onBeforeRender = function (e, t, o) {
    var renderTarget = e.getRenderTarget()
    module.mesh.material.uniforms.u_viewportHeight.value = renderTarget ? renderTarget.height : configuration.height
  }
  module.mesh.frustumCulled = false
  module.isReady = true
}

function update(date) {
  if (module.isReady) {
    if (!settings.useFloatPacking) {
      l.uniforms.u_positionTexture.value = s.currPositionRenderTarget.texture
    } else {
      l.uniforms.u_positionXYTexture.value = s.currPositionXYRenderTarget.texture
      l.uniforms.u_positionZWTexture.value = s.currPositionZWRenderTarget.texture
    }
    l.uniforms.u_time.value += date
    l.uniforms.u_lightDistance.value = configuration.pointCloudLightDistance
    l.uniforms.u_diffMultiplier.value = configuration.pointCloudDiffMultiplier
    l.uniforms.u_specMultiplier.value = configuration.pointCloudSpecMultiplier
    l.uniforms.u_initAnimation.value = configuration.pointCloudInitAnimation
    l.uniforms.u_particleScale.value = configuration.pointCloudParticleSize * settings.particleScale
    configuration.pointCloudColor1.setHex(configuration.pointCloudColor1Hex)
    configuration.pointCloudColor2.setHex(configuration.pointCloudColor2Hex)
    module.mesh.visible = c || configuration.pointCloudInitAnimation > 0
    c = false
  }
}

export default module


