import browser from './helpers/browser'

const State = {
  width: 0,
  height: 0,
  
  gl: null,
  renderer: null,
  camera: null,
  actualCamera: null,
  scene: null,
  controls: null,
  mouse2d: null,
  mouse3d: null,
  prevMouse3d: null,
  mouse3dEase: .3,
  mousePulseRadius: .5,
  mouseVelocityForce: 5,
  
  cameraShakePositionAmplitude: .1,
  cameraShakeRotationAmplitude: .1,
  cameraShakePositionFrequency: 15e-5,
  cameraShakeRotationFrequency: 2e-4,
  cameraShakePositionFrequency: 0,
  cameraShakeRotationFrequency: 0,
  
  resolution: null,
  pointLightPos: null,
  pointLightViewPos: null,
  pointLightColorHex: 4150914,
  pointLightColor: null,
  pointLightScatterDivider: 10,
  pointLightScatterPowInv: 1,
  pointLightUniforms: {},
  pointCloudColor1Hex: 1284,
  pointCloudColor1: null,
  pointCloudColor2Hex: 0x3f3f3f,
  pointCloudColor2: null,
  pointCloudLightDistance: 4.5,
  pointCloudDiffMultiplier: 2.4,
  pointCloudSpecMultiplier: 4,
  pointCloudInitAnimation: 0,
  pointCloudParticleSize: browser.isMobile ? 1.2 : .418,
  
  logoLightMultiplier: 2,
  
  ambientLightDistance: 8,
  ambientSpecMultiplier: 2.6122,
  ambientParticleSize: .5046,
  
  animation: 0,
  isReady: false,
  isReset: true,
  useAnimation: true,
  gridOpacity: 1,
  floorShadowOpacity: 0,
  showGrid: false,
  
  transitionParticleColorHex: 1257874,
  transitionParticleColor: null,
  transitionParticleDiffMultiplier: .5,
  transitionParticleSpecMultiplier: 1,
  transitionParticleLightDistance: .35,
  transitionParticleSize: 1,
  
  postRGBShift: .5,
  
  postBloomAmount: .2,
  postBloomRadius: 0,
  postBloomThreshold: .12,
  postBloomLenFlareOpacity: .01,
  
  postBokeh: 1,
  postBokehDiscSize: 1,
  postBokehRadius: .5,
  postBokehLow0: 0,
  postBokehLow1: 1,
  postBokehHigh0: 2,
  postBokehHigh1: 5,
  
  controlRotationSpeed: .4,
  transitionSimulatorSpeed: .0015,
  transitionSimulatorDieSpeed: .003,
  transitionSimulatorCurlSize: 1,
  transitionSimulatorEndAnimation: 0,
  transitionRenderCountRatio: 1,
  
  cameraAzimuthAngleRatio: 0,
  cameraAzimuthAngle: 0,
  cameraAzimuthAngleSpeed: .06,
  cameraAdditionalPolarAngle: 0,
  cameraAdditionalAzimuthAngle: 0,
  cameraAdditionalTargetPolarAngle: 0,
  cameraAdditionalTargetAzimuthAngle: 0,
  cameraAdditionalEaseRatio: .02,
  cameraAdditionalMovementScale: 0,
  
  throttleWindowResize: browser.isChromeIOS,
  
  volume: 1,
  opacity: 0,
}

if (typeof window !== 'undefined') {
  const loader = require('quick-loader')
  State.loader = loader.create()
}

export default State




